.submition-pop-up-work-ceo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  width: 50vw; 
  height: 30vh; 
  border-radius: 1vw; 
  padding: 4vw;
  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  box-shadow: 0px 0px 2vw 0px rgba(0, 0, 0, 0.75); 
}

  
  
.submition-pop-up-work-ceo input {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5vw;
  background-color: rgb(211, 208, 208);
  border-radius: 2vw;
  width: 60vw; 
  height: 10vh; 
  margin-left: 15vw; 
  margin: 0 auto;
  font-family: 'Kumbh Sans', sans-serif;
  color: black;
}

  
  .submition-pop-up-work-ceo input::placeholder{
    color: black;
    margin-left: 60%;
  }
  
  .submition-pop-up-work-ceo.evaluation-textarea .placeholder {
    color: black;
    align-items: left ;
    width: 70%;
    height: 10%;
  }
  
  .submition-pop-up-work-ceo h2{
    background-color: transparent;
    color: black;
    font-family: 'Kumbh Sans', sans-serif;
    margin-right: 50%;
    font-size: 2vw;
    font-weight: 200%; 

  }

  .circle {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: grey;
    margin-right: 5px;
  }
  
  .input-circle{
    display: flex;
    justify-content: space-around;
  }
  
  .span{
    display: flex;
    flex-direction: column;
    margin-left: 1%;
    margin-top: 5%;
  }
  
  .span-text {
    margin-left: 1%;
    width: 100%;
    margin-bottom: 5%;
  }
  .span-text  a{
    bottom: 5%;
  }

  .container-button-ceo{
    display: flex;
    justify-content: space-around;
  }
  
  .container-button-ceo .button1{
    display: flex;
    justify-content: space-around;
    margin-left: 5%;
    margin-right: 2%;
    width: 100vw;
    height: 5vh;
    background-color: #B7D6ED;
    border-radius: 20px;
    white-space: nowrap;
    overflow: hidden;
    font-size: 1.1vw;
    border: 1px solid transparent;
    cursor: pointer;
  }
  
  .container-button-ceo .button2{
    display: flex;
    justify-content: space-around;
    margin-left: 2%;
    margin-right: 2%;
    width: 100vw;
    height: 5vh;
    background-color: #F6B5B5;
    border-radius: 20px;
    white-space: nowrap;
    overflow: hidden;
    font-size: 1.1vw;
    border: 1px solid transparent;
    cursor: pointer;
  }
  
